import React from "react"
import { Button } from "react-bootstrap"
import { Link } from "gatsby"


const CardForm = ({cardFormData}) => {

return (
    <>
        {cardFormData.map((e,i) => {
            return(
            <div className="card-form-box" key={i}>
                <div className="card-form-box-content">
                    <img src={e.icon} alt="ring" />
                    <h4>{e.heading}</h4>
                    <p>{e.description}</p>
                    <Button>{e.buttonText}</Button>
                    <Link to="/">{e.linkText} <img src={e.linkIcon} alt="arrow" /></Link>
                </div>
            </div>
            )
        })}
    </>
)
}

export default CardForm